const Commodity = (props) => {
    return (
        <div className="box">
            <Grid data={data}
                columnNum={2}
                hasLine={false}
                itemStyle={{
                    width:'170px',
                    height:'260px',
                    backgroundColor:'#f5f5f9',
                    marginBottom:'9px'
                }}
                onClick={
                    () =>{props.history.push('/particulars/detail')}
                }
                renderItem={dataItem => (
                    <div className = 'box1'>
                        <img src={dataItem.image}  alt="#" />
                        <div className = 'words' >
                            <p className='p1'>{dataItem.description}</p>
                            <p className='p2'>
                                <span className='s1'>￥</span>
                                <span className='s2'>{dataItem.money}</span>
                                <span className='s3'>{dataItem.sum}</span>
                            </p>
                        </div>
                    </div>
                )}
            />
        </div>
    )
}

export default Commodity;
