import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';
class Partab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'yellowTab',
        };
    }
    render() {
        const { pathname } = this.props.location;
        return (
            <div style={{
                position: 'fixed',
                width: '124px',                
                bottom: 0,
                fontSize:'14px'
            }}>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="#FE560A"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="店铺"
                        key="dianpu"
                        icon={
                            <i className='iconfont icon-dianpu'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-ziyuan'></i>                           
                        }
                        selected={ this.state.selectedTab === 'yellowTab' }
                        
                        onPress={ () => {
                            this.props.history.push('/shop');
                        } }
                        data-seed="logId"
                    >
                    </TabBar.Item>

                    <TabBar.Item
                        title="客服"
                        key="kefu"
                        icon={
                            <i className='iconfont icon-taobaokefu-01'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-taobaokefu-01'></i>                           
                        }
                        
                        selected={ pathname === '/server' }
                        onPress={ () => {
                            this.props.history.push('/server');
                        } }
                        data-seed="logId1"
                    >                       
                    </TabBar.Item>

                    <TabBar.Item
                        title="收藏"
                        key="shoucang"
                        icon={
                            <i className='iconfont icon-shoucang'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-shoucang1'></i>                           
                        }                       
                        selected={ pathname === '/collect' }
                        onPress={ () => {
                            this.props.history.push('/collect');
                        } }
                    >    
                    </TabBar.Item>                    
			        			    
                </TabBar>
            </div>
        );
    }
}
export default withRouter(Partab);

