import React from 'react';
import './home.css';
import { SearchBar, Switch } from 'antd-mobile'
import HomeCarousel from './HomeCarousel';
import Mytab from './Mytab';
import Commodity from './Commodity';

const Home = (props) => {
    return (
        <div id = "home">
            <div className = "home1">
                <div className = "header1">
                    <i className='iconfont icon-tao' id='tao1'></i>
                </div>

                <div id = "home-search1">
                    <SearchBar
                        placeholder="寻找店铺宝贝"
                        onFocus={() => {
                            props.history.push('/home/detail')
                        }}
                    />                    
                </div>
            </div>

            <div className="home2">
                <HomeCarousel />
            </div>
            
            <div className="bounds">
                <img src='https://img.alicdn.com/tfs/TB1V2eQrKSSBuNjy0FlXXbBpVXa-966-114.png'/>
            </div>

            <div className = "home3" >
                <Commodity {...props}/>
            </div>
               
            <Mytab />
                    
        </div> 
               
    )
}

export default Home;


